/* ======================================================
   <!-- 并排列表 Side by Side List -->
/* ====================================================== */
@import '@uixkit/core/_global/scss/_variable-and-mixin.scss';
dl.uix-list-abreast {
    dt {
        float: right;
        clear: right;
        text-align: left;
    }
    dd {
        float: right;
        padding-right: 1rem;
        padding-left: auto;
        text-align: right;
    }
}
