/* ======================================================
    <!-- Form ( Layout )- -->
/* ====================================================== */
@import '@uixkit/core/_global/scss/_variable-and-mixin.scss';
@import '_variable-and-mixin.scss';
.uix-controls-layout {
    .uix-controls-layout__content {
        .uix-controls-layout__content__inner {
            > div {
                &:first-child {
                    margin-right: auto;
                    margin-left: 1.5rem;
                }
            }
        }
    }
    .uix-controls-layout__icon {
        margin-right: 1rem;
        margin-left: auto;
    }
}
@media all and (max-width: 768px) {
    .uix-controls-layout {
        .uix-controls-layout__content {
            .uix-controls-layout__content__inner {
                > div {
                    &:first-child {
                        margin-left: 0;
                    }
                }
            }
        }
    }
}